const initialState = {
  user: {
    firstName: "",
    lastName: "",
  },
};

const authenticationReducer = (state = initialState, action) => {
  switch (action.type) {
    case "SET_USER":
      return {
        ...state,
        user: {
          firstName: action.payload.firstName,
          lastName: action.payload.lastName,
        },
      };

    default:
      return state;
  }
};

export default authenticationReducer;
