import React from "react";
import { Link } from "react-router-dom";

let imgUrl =
  "https://previews.123rf.com/images/jagcz/jagcz1305/jagcz130500047/19434025-fresh-italian-pizza.jpg";
const bgStyle = {
  color: "white",
  backgroundImage: "url(" + imgUrl + ")",
  backgroundSize: "cover",
};
const Home = () => {
  return (
    <div className="flex h-screen items-center " style={bgStyle}>
      <div className="mx-auto container flex  flex-col items-center  py-4">
        <h1 className="text-6xl font-bold">Welcome Guardados!</h1>
        <div className="mx-auto container flex">
          <p>
            I'm a paragraph. Click here to add your own text and edit me. It’s
            easy. Just click “Edit Text” or double click me to add your own
            content and make changes to the font. Feel free to drag and drop me
            anywhere you like on your page. I’m a great place for you to tell a
            story and let your users know a little more about you.
          </p>
        </div>

        <Link to="/receipes">
          <button className="my-4 p-4 bg-teal-500 text-white text-3xl hover:bg-red-500">
            Our Receipes{" "}
          </button>
        </Link>
      </div>
    </div>
  );
};

export default Home;
