import React, { useEffect, useState } from "react";
import axios from "axios";
import Loader from "../Shared/Loader";

const Receipes = () => {
  const APP_ID = "87f7523d";
  const APP_KEY = "62d9e30c397544b2dbfa52ae73a2315f";
  /* Loader */
  const [loading, setLoading] = useState(false);
  const [receipes, setReceipes] = useState([]);
  const [search, setSearch] = useState("");
  const [query, setQuery] = useState("beef");
  useEffect(() => {
    setLoading(true);
    axios
      .get(
        `https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`,
        {
          withCredentials: true,
        }
      )
      .then((response) => {
        setReceipes(response.data.hits);
        setLoading(false);
      })
      .catch((error) => {
        setLoading(false);
      });
  }, [query]);

  useEffect(() => {
    document.body.style.backgroundColor = "#fcfcfc";
  });

  const SearchItem = (e) => {
    setSearch(e.target.value);
  };

  const getSearch = (e) => {
    e.preventDefault();
    setQuery(search);
  };
  return (
    <>
      {/* Loader enables on submit */}
      {loading === true && <Loader />}
      <div className="flex items-center bg-gray-300">
        <div className="mx-auto container">
          <div className=" flex px-2  items-center justify-between py-4">
            <h1 className="text-2xl text-red-800 font-bold w-4/6 leading-none">
              Receipes
            </h1>
            <div className="flex justify-end flex-col">
              <form onSubmit={getSearch} className="flex justify-end">
                <input
                  type="text"
                  className="p-2 rounded-tl rounded-bl "
                  value={search}
                  onChange={SearchItem}
                />
                <button
                  type="submit"
                  className="p-2 text-white rounded-tr rounded-br bg-red-800 "
                >
                  Search
                </button>
              </form>
              <p className="text-xs flex j  ">
                Search Keywords ( Checken, Coffee, ...)
              </p>
            </div>
          </div>
        </div>
      </div>
      <div className="mx-auto container flex  flex-col mb-10">
        <div className="mt-4 flex flex-wrap">
          {receipes &&
            receipes.map((receipe, index) => (
              <div className="w-1/4 mb-4 px-2 flex break-words" key={index}>
                <div className="bg-white border border-gray-300 rounded-lg flex flex-col">
                  <div className="relative">
                    <img
                      src={receipe.recipe.image}
                      alt={receipe.recipe.label}
                      className="rounded-t-lg w-full"
                    />
                    <span className="leading-none absolute left-0 bottom-0 font-bold px-2  text-white mb-4 ml-1 text-2xl">
                      {receipe.recipe.label}
                    </span>
                  </div>
                  <div className="p-4 flex flex-col">
                    <p className="text-xs font-bold underline mb-1">
                      Ingredient Lines
                    </p>
                    <ul className="flex flex-col text-xs text-gray-800">
                      {receipe.recipe.ingredientLines.map(
                        (ingredients, index) => (
                          <li className="break-words" key={index}>
                            {ingredients}
                          </li>
                        )
                      )}
                    </ul>
                  </div>
                </div>
              </div>
            ))}
        </div>
      </div>
    </>
  );
};

export default Receipes;
