import React from "react";
import { NavLink } from "react-router-dom";
import { connect } from "react-redux";
import isEmpty from "lodash/isEmpty";
import logo from "../assets/logo.png";

const Header = (props) => {
  return (
    <header className=" bg-biege-100  border-red-800 border-b-8">
      <div className=" mx-auto container flex  flex items-center justify-between">
        <div className="lg:w-1/4">
          <img src={logo} alt="" width="180" />
        </div>
        <div className="lg:w-2/4 ">
          <nav className=" flex  w-full items-center text-red-900 font-bold">
            <NavLink exact activeClassName="active" to="/" className="mx-2">
              Home
            </NavLink>
            <NavLink activeClassName="active" to="/receipes" className="mx-2">
              Receipes
            </NavLink>
            <NavLink activeClassName="active" to="/about" className="mx-2">
              About Us
            </NavLink>
            <NavLink activeClassName="active" to="/reach" className="mx-2">
              Reach Us
            </NavLink>
          </nav>
        </div>
        <div className="lg:w-1/4 text-right">
          <h2 className="text-lg font-bold">Call : 044-9898-7575</h2>
          {!isEmpty(props.user.firstName) && (
            <p>
              <strong>Welcome</strong> {props.user.firstName}{" "}
              {props.user.lastName}!
            </p>
          )}
        </div>
      </div>
    </header>
  );
};

const mapStateToProps = (state) => ({
  user: state.authenticationReducer.user,
});
export default connect(mapStateToProps)(Header);
